// CheckoutForm.js
import React from 'react';
import {injectStripe} from 'react-stripe-elements';
import ajaxWrapper from 'base/ajax.js';

import StripeCard from './stripecard.js';

class StripeForm extends React.Component {
    constructor(props) {
        super(props);
        this.stripeCallback = this.stripeCallback.bind(this);
        this.subscribeToAll = this.subscribeToAll.bind(this);
        this.postCallback = this.postCallback.bind(this);
    }

  handleSubmit = (ev) => {
    // We don't want to let default form submission happen here, which would refresh the page.
    ev.preventDefault();

    // Within the context of `Elements`, this call to createToken knows which Element to
    // tokenize, since there's only one in this group.
    this.props.stripe.createToken({name: this.props.user.email}).then(({token}) => {
      console.log('Received Stripe token:', token);
      console.log("Stripe User", this.props.user)
      ajaxWrapper("POST",'/stripe/', {'token':token.id, 'user_id':this.props.user.id, 'plan_id':this.props.plan_id}, this.stripeCallback);
    });

    // However, this line of code will do the same thing:
    //
    // this.props.stripe.createToken({type: 'card', name: 'Jenny Rosen'});

    // You can also use createSource to create Sources. See our Sources
    // documentation for more: https://stripe.com/docs/stripe-js/reference#stripe-create-source
    //
    // this.props.stripe.createSource({type: 'card', name: 'Jenny Rosen'});
  };

  stripeCallback(value) {
    console.log("Plan Id", this.props.plan_id)
    if (this.props.plan_id == 'All') {
      ajaxWrapper('GET','/api/home/course/',{},this.subscribeToAll)
    }
    else {
      console.log("Sending Call to add user to course", this.props.user.id)
      var data = {}
      data['users[]'] = JSON.stringify([this.props.user.id]);
      console.log("Users[]",data['users[]'])
      ajaxWrapper("POST",'/api/home/course/' + this.props.plan_id +'/', data, this.postCallback);
    }
  }

  subscribeToAll(result) {
    console.log("Subscribe To All", result);
    for (var index in result) {
      var course = result[index]['course'];
      var data = {}
      data['users[]'] = JSON.stringify([this.props.user.id]);
      console.log("Users[]",data['users[]'])
      ajaxWrapper("POST",'/api/home/course/' + course['id'] +'/', data, console.log);
    }

    this.postCallback(result)
  }

  postCallback(value) {
    window.location.href = '/course/' + this.props.plan_id + '/';
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <StripeCard />
        <button className="btn btn-success">Confirm order</button>
      </form>
    );
  }
}

export default injectStripe(StripeForm);
