import React, { Component } from 'react';

import Wrapper from 'base/wrapper.js';
import ajaxWrapper from 'base/ajax.js';
import {Container, Button, Image, Form, TextInput, Navbar, List, Link, Accordion, Paragraph, RadioButton, TextArea, Header, Card} from 'library';

class CourseVideos extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
            videos:[],
            course: {'name':''}
        };

        this.videoCallback = this.videoCallback.bind(this);
    }

    componentDidMount() {
      ajaxWrapper("GET",'/api/home/video/?order_by=order&related=course&course=' + this.props.course_id,{}, this.videoCallback)
    }

    videoCallback(result) {

      var videos = []
      for (var index in result) {
        var video = result[index]['video']
        videos.push(video)
      }
      if (video) {
        this.setState({'videos':videos, 'loaded':true, 'course':video['course']})
      }
      else {
        this.setState({'videos':videos, 'loaded':true})
      }

    }

    render() {

      var content = null;
      if (this.state.loaded) {
        var videos = []
        for (var index in this.state.videos) {
          var video = this.state.videos[index];
          var buttons = <div><Button href={'/editVideo/' + this.props.course_id + "/" + video['id'] + '/'} type={'info'} text={'Edit Video'} /><Button href={'/video/' + video['id'] + '/'} type={'primary'} text={'View Video'} /></div>;
          videos.push(<Card name={video['name']} cssClass={'col-md-4'} imageUrl={video['image']} buttons={buttons} />);
        }

        var content = <div className="container">
            <Header text={'Videos For Course: ' + this.state.course.name} size={2} />
            <Button type={'success'} text={'Add New Video'} href={"/editVideo/" + this.props.course_id + "/"} />
            <div className="row">
            {videos}
            </div>
          </div>;
      }


        return (
            <Wrapper loaded={this.state.loaded} content={content} />
        );
    }
}

export default CourseVideos;
