import React, { Component } from 'react';

import Wrapper from 'base/wrapper.js';
import ajaxWrapper from 'base/ajax.js';
import {Container, Button, Image, Form, TextInput, Navbar, List, Link, Accordion, Paragraph, RadioButton, TextArea, Header} from 'library';
import Card from 'projectLibrary/courseCard.js';

class ViewCourses extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
            link: '',
            name: '',
            description: '',
            image: '',
        };

        this.courseCallback = this.courseCallback.bind(this);
    }

    componentDidMount() {
      ajaxWrapper("GET",'/api/home/course/?order_by=order',{}, this.courseCallback)
    }

    courseCallback(result) {
      var courses = []
      for (var index in result) {
        var course = result[index]['course']
        courses.push(course)
      }

      this.setState({'courses':courses, 'loaded':true})
    }

    render() {

      var content = null;
      if (this.state.loaded) {
        var courses = []
        for (var index in this.state.courses) {
          var course = this.state.courses[index];
          if (this.props.user.is_staff == true) {
            var buttons = <div><Button href={'/editCourse/' + course['id'] + '/'} type={'info'} text={'Edit Course'} /><Button href={'/course/' + course['id'] + '/'} type={'primary'} text={'View Course'} /><Button href={'/courseVideos/' + course['id'] + '/'} type={'success'} text={'Manage Videos'} /></div>;
          }
          else {
            var buttons = <div><Button href={'/course/' + course['id'] + '/'} type={'primary'} text={'View Course'} /></div>;
          }
          courses.push(<Card name={course['name']} price={course['price']} description={course['description']} buttons={buttons} imageUrl={'/static/images/baseImage.jpg'} cssClass={'col-md-4'} />);
        }

        var addNew = <div></div>;
        var bulk_price = null;
        if (this.props.user.is_staff == true) {
          var addNew = <Button type={'success'} text={'Add New Course'} href={"/editCourse/"} />
          bulk_price = <Button type={'primary'} text={'Edit Price For Bulk Purchase'} href={"/editPrice/"} />
        }

        var content = <div className="container">
          <br />
            {bulk_price}
            <Header text={'All Courses'} size={2} />
            {addNew}
            <div className="row">
              {courses}
            </div>
          </div>;
      }


        return (
            <Wrapper loaded={this.state.loaded} content={content} />
        );
    }
}

export default ViewCourses;
