import React, { Component } from 'react';

import Wrapper from 'base/wrapper.js';
import ajaxWrapper from 'base/ajax.js';
import {Container, Button, Image, Form, TextInput, Navbar, List, Link, Accordion, Paragraph, RadioButton, TextArea, Header, Card, HTMLInputDisplay} from 'library';

class Video extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
            link: '',
            name: '',
            description: '',
            image: '',
            bought: false,
        };

        this.videoCallback = this.videoCallback.bind(this);
    }

    componentDidMount() {
      ajaxWrapper("GET",'/api/home/video/' + this.props.video_id + '/?related=course,course__videos,course__users',{}, this.videoCallback)
    }


    videoCallback(result) {
      var video = result[0]['video']
      console.log("Setting User State")
      var bought = false;
      for (var index in video['course']['users']) {
          var user = video['course']['users'][index]['user'];
          if (user.id == this.props.user.id) {
            bought = true;
          }
      }
      video['bought'] = bought
      video['loaded'] = true;
      video['course']['users'] = null;

      this.setState(video)
    }



    render() {

      var content = null;

      if (this.state.loaded == true) {
        console.log("Here")
        var video = <img src={this.state.image} style={{'width':'960px', 'height':'640px'}} />

        var bought = null;
        if (this.props.user_id == "") {
          if (this.state.course.price > 0) {
            bought = <a href={'/signUp/'}>Sign Up and Purchase the course here for ${this.state.course.price.toFixed(2)}</a>
          }
          else {
            bought = <a href={'/signUp/'}>Sign Up To Access This Course For Free</a>
          }
        }
        else if (this.state.course.price > 0) {
          bought = <a href={'/purchase/' + this.state.course.id + '/'}>Purchase the course here for ${this.state.course.price.toFixed(2)}</a>
        }

        if (this.state.bought == true || this.props.user.is_staff == true || (this.state.course.price == 0 && this.props.user_id != '')) {
          bought = null;
          video = <iframe src={this.state.link} width={960} height={640} seamless frameborder={0} allowfullscreen></iframe>
        }
        if (this.props.user.is_staff == true) {
          bought = <Button href={'/editVideo/' + this.state.course.id + '/' + this.props.video_id + '/'} type={'primary'} text={'Edit Video'} />
        }

        var videos = [];
        for (var index in this.state.course['videos']) {
          var tempVideo = this.state.course['videos'][index]['video'];
          if (tempVideo.id != parseInt(this.props.video_id)) {
            videos.push(<Card cssClass={'col-md-4'} imageUrl={tempVideo['image']} name={tempVideo['name']} button_type={'primary'} button={'View'} link={'/video/' + tempVideo['id'] + '/'} />)
          }
        }

        var courseLink = <span>Other Videos In <a href={"/course/" + this.state.course.id + "/"}>{this.state.course.name}</a></span>

        content =
          <div className="container">
          <div>
            {video}
            <Header size={2} text={this.state.name} />
            {bought}
            <HTMLInputDisplay value={this.state.description} />
            <br />
            <br />
            <Header size={3} text={courseLink} />
            <div className='row'>
            {videos}
            </div>
            <br />
            <br />
            <div style={{'textAlign':'center'}}>
              <Button type={'success'} text={'See All Courses'} href={'/viewCourses/'} />
            </div>
          </div>
          </div>;
      }

        return (
            <Wrapper loaded={this.state.loaded}  content={content} />
        );
    }
}

export default Video;
