import React, { Component } from 'react';
import ajaxWrapper from "base/ajax.js";
import Wrapper from 'base/wrapper.js';

import {Form, TextInput, Select, PasswordInput, Header, TextArea, NumberInput, ImageInput, HTMLInput} from 'library';
import Navbar from 'projectLibrary/nav.js';

class EditVideo extends Component {
    constructor(props) {
        super(props);

        this.state = {'name':'', 'description':'','order':1, 'link':'', 'image':'', 'loaded':false, 'course': this.props.course_id, 'id':''};

        this.courseCallback = this.courseCallback.bind(this);
    }

    componentDidMount(value) {
      console.log("Form Value",value)
        if (this.props.video_id) {
          ajaxWrapper('GET','/api/home/video/' + this.props.video_id + '/', {}, this.courseCallback);
        }
        else {
          this.setState({loaded:true})
        }
    }

    courseCallback(result) {
      var video = result[0]['video'];
      console.log(result)
      console.log("Video",video)
      video['loaded'] = true;
      video['course'] = video['course_id']
      this.setState(video)
    }

    render() {

        var Components = [TextInput, HTMLInput, TextInput, NumberInput, ImageInput, Select];
        var name = {'value':'','name':'name','label':'Name:','placeholder': 'Name'}
        var description = {'value':'','name':'description','label':'Description','placeholder': 'Course Description'}
        var link = {'value':'','name':'link','label':'Video Link','placeholder': "www.vimeo..."}
        var order = {'value':'','name':'order','label':'Order:','placeholder': 1}
        var image = {'value':'','name':'image','label':'Image', 'multiple':false}
        var course = {'value':'','name':'course','label':'Course', 'optionsUrl':'/api/home/course/', 'optionsUrlMap':{'text':['course','name'],'value':['course','id']}}

        var ComponentProps = [name, description, link, order, image, course];
        var defaults = this.state;

        var submitUrl = "/api/home/video/";
        if (this.props.video_id) {
          submitUrl += this.props.video_id + '/';
        }

        var deleteUrl = undefined;
        if (this.props.video_id) {
          deleteUrl = "/api/home/video/" + this.props.video_id + "/delete/";
        }

        var title = <Header text={'Create New Video'} size={2} />
        if (this.props.video_id) {
          title = <Header text={'Edit Video: ' + this.state.name} size={2} />
        }


        if (this.state.loaded) {
        var content = <div className="container">
                {title}
                <Form components={Components} redirectUrl={"/courseVideos/" + this.props.course_id + '/'} componentProps={ComponentProps} deleteUrl={deleteUrl} submitUrl={submitUrl} defaults={defaults} />
        </div>;
      }
      else {
        var content = null;
      }


        return (
          <div>
            <Wrapper loaded={this.state.loaded} content={content} />
          </div>
             );
    }
}
export default EditVideo;
