import React, { Component } from 'react';

import Wrapper from 'base/wrapper.js';
import ajaxWrapper from 'base/ajax.js';
import {Container, Button, Image, Form, TextInput, Navbar, List, Link, Accordion, Paragraph, RadioButton, TextArea, Header, Card} from 'library';

class LoggedIn extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
            courses: [],
        };

        this.courseCallback = this.courseCallback.bind(this);
    }

    componentDidMount() {
      ajaxWrapper("GET",'/api/home/course/?related=videos',{}, this.courseCallback)
    }

    courseCallback(result) {
      var courses = []
      for (var index in result) {
        courses.push(result[index]['course'])
      }
      this.setState({courses:courses, loaded:true})
    }

    setGlobalState() {

    }

    render() {
      var courses = [];
      for (var index in this.state.courses) {
        var videos = [];
        for (var i in this.state.courses[index]['videos']) {
          var video = this.state.courses[index]['videos'][i]['video'];
          var videoCard = <div class="col-md-3"><Card imageUrl={video['image']} title={video['title']} description={video['description']} link={'/video/' + video['id']} button_type={'success'} button={'View'} /></div>
          videos.push(videoCard);
        }

        var course =
          <div>
            <Header size={2} text={this.state.courses[index]['name']} css={{'marginBottom':'25px'}} />
            <div className="row">
              {videos}
            </div>
          </div>
        courses.push(course)
      }

      var content =
        <div className="container">
          {courses}
        </div>;



        return (
            <Wrapper loaded={this.state.loaded}  content={content} />
        );
    }
}

export default LoggedIn;
